import React, { Component } from 'react';
import { Motion, spring } from 'react-motion';

export default class AnimatedSVG extends Component {
  constructor(props) {
    super(props);

    this.state = {open: this.props.open}
  }

  render() {
    return (
      <div className="Squiggle">
        <Motion style={{x: spring(this.props.open ? 0 : 100, {stiffness: 90, damping: 30})}}>
          {({x}) =>
            <svg width="580px" height="220px" viewBox="0 0 580 220" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <g id="Artboard" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                    <path d="M75,96 C26,111 30,147 75,152 C121,157 121,50 155,96 C188,143 248,192 286,146 C324,100 347,61 320,42 C292,23 192,136 320,146 C370,150 444,148 540,141" id="Path" stroke="#979797" strokeWidth="6" fillRule="nonzero" strokeDasharray="904.2 904.2" strokeDashoffset={904.2 * (x / 100)}></path>
                </g>
            </svg>
          }
        </Motion>
      </div>
    );
  }
}
