import React, { Component } from 'react';
import ControlBlock from './ControlBlock';
import LeftBlock from './LeftBlock';
import BottomBlock from './BottomBlock';
import RightBlock from './RightBlock';
import Header from './Header';
import AnimatedSVG from './AnimatedSVG';
import './App.css';

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pathOpen: false,
      leftOpen: false,
      bottomOpen: false,
      rightOpen: false
    };
  }

  render() {
    return (
      <div className="App">
        <Header>
          <ControlBlock>
            <button onClick={() => this.setState({leftOpen: !this.state.leftOpen})}>
              Left Pane
            </button>

            <button onClick={() => this.setState({bottomOpen: !this.state.bottomOpen})}>
              Bottom Pane
            </button>

            <button onClick={() => this.setState({rightOpen: !this.state.rightOpen})}>
              Right Pane
            </button>

            <button onClick={() => this.setState({pathOpen: !this.state.pathOpen})}>
              Path
            </button>
          </ControlBlock>
        </Header>
        <br/>
        <AnimatedSVG open={this.state.pathOpen}/>
        <LeftBlock open={this.state.leftOpen}/>
        <BottomBlock open={this.state.bottomOpen}/>
        <RightBlock open={this.state.rightOpen}/>
      </div>
    );
  }
}
